@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  background: #e0eafc;
  background: -webkit-linear-gradient(to right, #cfdef3, #e0eafc);
  background: linear-gradient(to right, #cfdef3, #e0eafc);
  padding: 20px 0;
}

.container {
  width: 320px;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

form {
  display: flex;
  position: relative;
}

h1 {
  font-size: 4rem;
  color: #d980fa;
}

.input {
  padding: 6px 10px;
  border: none;
  font-family: inherit;
  margin-bottom: 20px;
  box-shadow: 0 0 10px 4px rgba(0, 0, 0, 0.1);
}

.input::placeholder {
  color: #686d74;
}

.input:focus {
  outline: 1px solid #d980fa;
}

.btn-add {
  position: absolute;
  right: 0;
  background-color: transparent;
  border: none;
  width: 24px;
  height: 32px;
  cursor: pointer;
}

.btn-add:hover {
  color: #d980fa;
}

.todos {
  width: 100%;
  list-style: none;
}

.todo {
  position: relative;
  background-color: white;
  margin: 10px 0;
  padding: 5px 10px;
  font-size: 12px;
  border-radius: 5px;
  cursor: pointer;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
    opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.todo-out {
  transform: translateX(100px);
  opacity: 0;
}

.btn-delete {
  position: absolute;
  right: 10px;
  background-color: transparent;
  color: #d980fa;
  border: none;
  width: 18px;
  height: 18px;
  font-size: 18px;
  cursor: pointer;
}

.is-hidden {
  display: none;
}

.checked {
  text-decoration-line: line-through;
  text-decoration-color: #d980fa;
  color: #e9bff8;
}

.radio-todos {
  list-style: none;
  display: flex;
  background-color: #eef1f7;
  font-weight: 300;
  font-size: 14px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.radio-btn {
  color: #686d74;
  cursor: pointer;
  background-color: transparent;
  border: none;
  font-family: inherit;
  padding: 5px 10px;
}

.radio-checked {
  background-color: #e9bff8;
}
